  /**
   * 
   * @param title 
   * @param desc 
   * @param topic 
   * @param content 
   * @param endHeight 
   * ~~~
   * 
   * 1.
   * //
   * let topic = 'gateway_register'
   * //content,
   * let name = 'aschCoin'   //3-16
   * let desc = 'test the gateway register'
   * let minimumMembers = 3      //3-33
   * let updateInterval = 8640   //8640
   * let symbol = 'TEC'   //TEC
   * let currencyDesc = 'some describes of currency'    //
   * let precision = 1      //
   * let currency = {symbol:symbol,
   *            desc:currencyDesc,
   *           precision:precision}
   * //content
   * let content = {name:name,
   *           desc:desc,
   *           minimumMembers:minimumMembers,
   *           updateInterval:updateInterval,
   *           currency:currency}
   * 2. 
   * //
   * let topic = 'gateway_init'
   * //content
   * let gateway = 'bitcoin'     //
   * let members = [             //
   * 'A5eTVn2Mz5p2j6SjGKdgvmUc2vMsSvKzuy',
   * 'A3SmW61ZwxmNc26BbfKLbHkaNbmUQzexuj',
   * 'A4ncaYtKRrD8YS2Mi82HbwGEE9DxqsbEr9']
   * //content
   * let content = {gateway:gateway,
   *            members:members
   *          }
   * 
   * 3.
   * //
   * let topic = 'gateway_update_member'
   * //content
   * let gateway = 'bitcoin'     //
   * let from = 'A3SmW61ZwxmNc26BbfKLbHkaNbmUQzexuj'   //
   * let to = 'A7w7Rx5bCerJFbfG5BKdQ77bPqfWeyrmgJ'     //
   * //content
   * let content = {gateway:gateway, from:from, to:to}
   * 
   * 4.
   * //
   * let topic = 'gateway_revoke'     //
   * //content
   * let gateway = 'bitcoin'     //
   * //content
   * let content = {gateway:gateway}
   * 
   * ~~~
   */
  public async createProposal(
    title: string,
    desc: string,
    topic: string,
    content: string,
    endHeight: number
  ): Promise<object> {
    let trx: Transaction = TransactionBuilder.createProposal(title, desc, topic, content, endHeight)
    trx = await this.aschWeb.sign(trx)
    return this.broadcastTransaction(trx)
  }

